@import url('https://fonts.googleapis.com/css2?family=Lato:wght@100;300;400;700&family=Montserrat:wght@100&family=Open+Sans:wght@300&family=Raleway:wght@300;400&family=Roboto:wght@100&display=swap');

html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    background-color: #313B47;
    font-family: 'Lato', Arial, sans-serif;
}

.resume-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.resume {
    width: 1100px;
    background-color: #ffffff;
    padding: 20px 50px;
    display: flex;
    flex-direction: column;
}

.name {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-size: 35px;
}

.personal-info {
    margin-bottom: 20px;
    display: flex;
    flex-direction: row;
    font-size: 20px;
    font-weight: 300;
}

.section {
    margin: 20px 10px;
}

.section-name {
    font-family: 'Lato';
    font-weight: 100;
    font-size: 30px;
    /*color: #2c3645;*/
}

.heading {
    display: flex;
    flex-direction: row;
}

.heading-main {
    display: flex;
    flex-direction: row;
}

.heading-main-one {
    font-weight: 700;
    font-size: 25px;
}

.heading-main-two {
    font-family: 'Raleway';
    font-weight: 400;
    font-size: 20px;
    padding-top: 5px;
}

.pipe {
    /*width: 0;*/
    height: 15px;
    margin: 0 10px;
    padding-top: 7px;
    /*margin-top: 5px;*/
    /*padding: 0 0;*/
    /*border-style: solid;*/
    /*border-width: 3px;*/
}

.info-pipe {
    padding: 0 0;
}

.heading-side {
    margin-left: auto;
    padding-top: 5px;
    font-family: 'Raleway';
    font-size: 20px;
    font-weight: 400;
}

ul {
  margin: 5px;
}

.text {
    font-weight: 300;
    font-size: 20px;
}

.raleway {
    font-family: 'Raleway';
    font-size: 25px;
}

.text-flex {
    display: flex;
    flex-direction: row;
}

.edit-pane {
    display: flex;
    flex-direction: row;
    }

.edit-button {
    width: 25px;
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-style: solid;
    border-width: 2px;
    border-radius: 4px;
    color: #ffffff;
    background-color: #00C8AA;
    font-weight: 400;
}

.edit-button:hover {
    cursor: pointer;
    background-color: #00A288;
}

.pdf-btn {
    width: 150px;
    height: 25px;
    margin: 20px auto;
    padding: 10px 20px;
    border-radius: 6px;
    background-color: #00C8AA;
    color: #ffffff;
    font-size: 20px;
    text-align: center;
}

.pdf-btn:hover {
    cursor: pointer;
    background-color: #00A288;
}

span {
    min-width: 20px;
}
